import { Suspense, createRef, useContext, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import description from "../../../assets/icons/description.svg";
import help from "../../../assets/icons/help.svg";
import palette from "../../../assets/icons/palette.svg";
import { Context } from "../../../config/context";
import { paints } from "../../../constants/paintsConfig";
import {
  LinkToViber,
  LinkToViber2,
  LinkToViber4,
  ViberContactButton2,
  ViberContactButton3,
} from "../components/LinkToViber";
import { Slider } from "../components/Slider";
import { contentConfig } from "../constants/contentConfig";
import {
  BoldText,
  Container,
  ControlPanel,
  FallbackContent,
  FileDownloadButton,
  InfoContainer,
  LinkButton,
  LinkContainer,
  ManufacturerText,
  PaintPicture,
  PaintPriceText,
  PaintTitle,
  PictureContainer,
  ProductWrapper,
  ShortDescription,
} from "./styles";

const Product = () => {
  const { tablet, smartphone } = useContext(Context);
  const { category, name } = useParams();
  const contentRef = createRef(null);

  const [activeContent, setActiveContent] = useState("Description");
  const [render, setRender] = useState(false);

  const Content = contentConfig[activeContent];
  const currentPaint = paints[category].find((paint) => paint.path === name);
  const {
    images: { product, products },
    header,
    link,
    purchase,
  } = currentPaint;

  const showActiveContent = (signContent) => {
    setActiveContent(signContent);

    if (tablet) {
      const HEADER_PADDING = 100;
      const contentPosition = contentRef?.current.getBoundingClientRect();

      window.scrollTo({
        top: contentPosition.top + window.pageYOffset - HEADER_PADDING,
        behavior: "smooth",
      });
    }
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    setRender(true);
  }, []);

  return (
    <Container render={render}>
      <ShortDescription>
        <PaintTitle>{header}</PaintTitle>
      </ShortDescription>

      <ProductWrapper>
        <InfoContainer>
          <ManufacturerText>
            <BoldText>–ò–∑–≥–æ—Ç–æ–≤–∏—Ç–µ–ª—å: </BoldText>–û–û–û "–≠–∫–∑–∞–ª—å—Ç", —É–ª. –¶–µ–Ω—Ç—Ä–∞–ª—å–Ω–∞—è,
            –¥. 49, –î–∑–µ—Ä–∂–∏–Ω—Å–∫–∏–π —Ä–∞–π–æ–Ω, –ë–æ—Ä–æ–≤—Å–∫–æ–π —Å/—Å, –¥. –ë–∞–∫–∏–Ω–æ–≤–æ 222720, –ú–∏–Ω—Å–∫–∞—è
            –æ–±–ª., –†–µ—Å–ø—É–±–ª–∏–∫–∞ –ë–µ–ª–∞—Ä—É—Å—å
          </ManufacturerText>

          <ManufacturerText style={{ paddingTop: "1.5rem" }}>
            <BoldText>–°—Ç—Ä–∞–Ω–∞ –ø—Ä–æ–∏–∑–≤–æ–¥—Å—Ç–≤–∞: </BoldText>–ë–µ–ª–∞—Ä—É—Å—å
          </ManufacturerText>
        </InfoContainer>
      </ProductWrapper>

      {tablet ? (
        <PictureContainer>
          <PaintPicture src={products} />
        </PictureContainer>
      ) : (
        <Slider images={product} />
      )}

      <ProductWrapper>
        {purchase.map(({ price, amount }) => {
          return (
            <PaintPriceText key={`product_price_${price}`}>
              –û—Ç <BoldText>{price}</BoldText> —Ä—É–±. –∑–∞ –≤–µ–¥—Ä–æ {amount}.
            </PaintPriceText>
          );
        })}

        <LinkToViber />
        <LinkToViber2 />
        <ViberContactButton2 />
        <ViberContactButton3 />
        <LinkToViber4 />

        <InfoContainer>
          <ControlPanel>
            {smartphone ? (
              <LinkContainer
                className={activeContent === "Description" ? "active" : null}
                onClick={() => showActiveContent("Description")}
              >
                <PaintPicture src={description} />
              </LinkContainer>
            ) : (
              <LinkButton
                className={activeContent === "Description" ? "active" : null}
                onClick={() => showActiveContent("Description")}
                image={description}
              >
                –û–ø–∏—Å–∞–Ω–∏–µ
              </LinkButton>
            )}

            {smartphone ? null : (
              <LinkButton
                className={activeContent === "Application" ? "active" : null}
                onClick={() => showActiveContent("Application")}
              >
                –ù–∞–Ω–µ—Å–µ–Ω–∏–µ
              </LinkButton>
            )}

            {smartphone ? null : (
              <LinkButton
                className={
                  activeContent === "Characteristics" ? "active" : null
                }
                onClick={() => showActiveContent("Characteristics")}
              >
                –•–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏
              </LinkButton>
            )}

            {smartphone ? (
              <LinkContainer
                className={
                  activeContent === "PaletteContainer" ? "active" : null
                }
                onClick={() => showActiveContent("PaletteContainer")}
              >
                <PaintPicture src={palette} />
              </LinkContainer>
            ) : (
              <LinkButton
                className={
                  activeContent === "PaletteContainer" ? "active" : null
                }
                onClick={() => showActiveContent("PaletteContainer")}
              >
                –¶–≤–µ—Ç–æ–≤–∞—è –ø–∞–ª–∏—Ç—Ä–∞
              </LinkButton>
            )}

            {smartphone ? (
              <LinkContainer
                className={activeContent === "Delivery" ? "active" : null}
                onClick={() => showActiveContent("Delivery")}
              >
                <PaintPicture src={help} />
              </LinkContainer>
            ) : (
              <LinkButton
                className={activeContent === "Delivery" ? "active" : null}
                onClick={() => showActiveContent("Delivery")}
              >
                –£—Å–ª–æ–≤–∏—è –¥–æ—Å—Ç–∞–≤–∫–∏ –∏ —Å–∞–º–æ–≤—ã–≤–æ–∑–∞
              </LinkButton>
            )}

            {smartphone ? null : (
              <FileDownloadButton href={link} target="_blank">
                –¢–µ—Ö. –¥–æ–∫—É–º–µ–Ω—Ç PDF
              </FileDownloadButton>
            )}
          </ControlPanel>

          <Suspense fallback={<FallbackContent>–ó–∞–≥—Ä—É–∑–∫–∞...</FallbackContent>}>
            <Content ref={contentRef} paint={currentPaint} product />
          </Suspense>

          <LinkToViber2 />
        </InfoContainer>
      </ProductWrapper>
    </Container>
  );
};

export default Product;
