import styled, { keyframes } from "styled-components";

// –ê–Ω–∏–º–∞—Ü–∏—è –ø—É–ª—å—Å–∞—Ü–∏–∏ –¥–ª—è –ø—Ä–∏–≤–ª–µ—á–µ–Ω–∏—è –≤–Ω–∏–º–∞–Ω–∏—è
const pulse = keyframes`
  0% {
    box-shadow: 0 0 0 0 rgba(227, 6, 19, 0.7);
  }
  70% {
    box-shadow: 0 0 0 10px rgba(227, 6, 19, 0);
  }
  100% {
    box-shadow: 0 0 0 0 rgba(227, 6, 19, 0);
  }
`;

const shake = keyframes`
  0%, 100% { transform: translateX(0) translateY(-2px); }
  25% { transform: translateX(-1px) translateY(-2px); }
  75% { transform: translateX(1px) translateY(-2px); }
`;

export const ViberContainer3 = styled.div`
  display: flex;
  justify-content: center;
  margin: 25px 0;
  padding: 0 15px;
`;

export const ViberButton3 = styled.a`
  /* –û—Å–Ω–æ–≤–Ω—ã–µ —Å—Ç–∏–ª–∏ –≤ —Å—Ç–∏–ª–µ —Å–∞–π—Ç–∞ */
  display: inline-flex;
  align-items: center;
  gap: 12px;
  background: #e30613; /* –ö—Ä–∞—Å–Ω—ã–π –∫–∞–∫ –Ω–∞ —Å–∞–π—Ç–µ */
  color: white;
  text-decoration: none;

  /* –†–∞–∑–º–µ—Ä—ã –∏ –æ—Ç—Å—Ç—É–ø—ã */
  padding: 18px 35px;
  border-radius: 8px; /* –ë–æ–ª–µ–µ –º—è–≥–∫–æ–µ —Å–∫—Ä—É–≥–ª–µ–Ω–∏–µ –∫–∞–∫ –Ω–∞ —Å–∞–π—Ç–µ */

  /* –¢–µ–∫—Å—Ç */
  font-family: "Roboto", "Inter", -apple-system, BlinkMacSystemFont, sans-serif;
  font-size: 18px;
  font-weight: 700; /* –ñ–∏—Ä–Ω—ã–π –∫–∞–∫ –Ω–∞ —Å–∞–π—Ç–µ */
  line-height: 1.2;
  text-transform: uppercase; /* –ö–∞–ø—Å –∫–∞–∫ –Ω–∞ –∫–Ω–æ–ø–∫–∞—Ö —Å–∞–π—Ç–∞ */
  letter-spacing: 0.5px;

  /* –¢–µ–Ω–∏ –∏ —ç—Ñ—Ñ–µ–∫—Ç—ã */
  box-shadow: 0 4px 12px rgba(227, 6, 19, 0.3), 0 2px 4px rgba(0, 0, 0, 0.15);

  /* –ü–ª–∞–≤–Ω—ã–µ –ø–µ—Ä–µ—Ö–æ–¥—ã */
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  position: relative;
  overflow: hidden;
  border: 2px solid #e30613;
  cursor: pointer;

  /* –ê–Ω–∏–º–∞—Ü–∏—è –ø—Ä–∏–≤–ª–µ—á–µ–Ω–∏—è –≤–Ω–∏–º–∞–Ω–∏—è */
  animation: ${pulse} 2s infinite;

  /* –ü—Å–µ–≤–¥–æ-—ç–ª–µ–º–µ–Ω—Ç –¥–ª—è –≥—Ä–∞–¥–∏–µ–Ω—Ç–∞ –ø–æ–≤–µ—Ä—Ö */
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(
      135deg,
      rgba(255, 255, 255, 0.1) 0%,
      rgba(255, 255, 255, 0) 50%,
      rgba(255, 255, 255, 0.1) 100%
    );
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  /* Hover —ç—Ñ—Ñ–µ–∫—Ç—ã */
  &:hover {
    background: #c80511; /* –¢–µ–º–Ω–µ–µ –ø—Ä–∏ hover –∫–∞–∫ –Ω–∞ —Å–∞–π—Ç–µ */
    border-color: #c80511;
    transform: translateY(-2px);
    box-shadow: 0 6px 20px rgba(227, 6, 19, 0.4), 0 3px 8px rgba(0, 0, 0, 0.2);
    animation: none; /* –£–±–∏—Ä–∞–µ–º –ø—É–ª—å—Å–∞—Ü–∏—é –ø—Ä–∏ hover */

    &::after {
      opacity: 1;
    }
  }

  /* –ê–∫—Ç–∏–≤–Ω–æ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ */
  &:active {
    transform: translateY(0);
    box-shadow: 0 2px 8px rgba(227, 6, 19, 0.4), 0 1px 3px rgba(0, 0, 0, 0.15);
    background: #b0040f;
  }

  /* Focus –¥–ª—è –¥–æ—Å—Ç—É–ø–Ω–æ—Å—Ç–∏ */
  &:focus {
    outline: none;
    border-color: #ffffff;
    box-shadow: 0 0 0 3px rgba(227, 6, 19, 0.5),
      0 4px 12px rgba(227, 6, 19, 0.3);
  }

  /* –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å */
  @media (max-width: 768px) {
    padding: 16px 28px;
    font-size: 16px;
    width: 100%;
    max-width: 320px;
    justify-content: center;
  }

  @media (max-width: 480px) {
    padding: 14px 24px;
    font-size: 15px;
    gap: 8px;
  }
`;

export const ViberIcon3 = styled.span`
  font-size: 22px;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
  transition: all 0.3s ease;

  ${ViberButton3}:hover & {
    transform: scale(1.15);
  }
`;

export const ViberText3 = styled.span`
  white-space: nowrap;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);

  @media (max-width: 480px) {
    font-size: 14px;
  }
`;

// –í–∞—Ä–∏–∞–Ω—Ç —Å –∏–∫–æ–Ω–∫–æ–π Viber (–µ—Å–ª–∏ –Ω—É–∂–Ω–æ)
export const ViberLogo3 = styled.svg`
  width: 22px;
  height: 22px;
  fill: white;
  filter: drop-shadow(0 1px 2px rgba(0, 0, 0, 0.2));
  transition: all 0.3s ease;

  ${ViberButton3}:hover & {
    transform: scale(1.15);
  }
`;
